
//設定ファイルの読み込み
import setting from '../assets/setting.json';//一般設定
import floatList from '../assets/floatList.json';//フロートの一覧（読み込みファイルなど）
import bldgList from '../assets/bldgList.json';//建物データの一覧（読み込みファイルなど）

import data from '../assets/TestData.json';
import mapboxgl,{ ErrorEvent } from 'mapbox-gl';
import 'mapbox-gl/dist/mapbox-gl.css';

import { defineComponent, onMounted, reactive, toRefs, watch, ref, effect } from 'vue';
//three.jsのコンポーネントをインストール
import * as THREE from 'three';
//import {THREE} from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader';
import { DDSLoader } from 'three/examples/jsm/loaders/DDSLoader';

export default defineComponent({
    /*
    method(){
        increment(){
            this.count += 1;
            this.$emit("add", this.count);
        }
    },
    */
    emits:['add','uploadBldgId'],
    name: 'HelloWorld',
    props: {
        //(1)ハイライト表示する建物のIDのリストが渡ってくる
        highlightBldgIds:Array,
    },
    setup(props, context) {
        //作業用フラグ（建物IDなど調べるとき）
        var debug = true;

        const tgtBldgIds: Array<number> = [];
        for (const i in data.BuildingInfo){
            var tgtBldgId = parseInt(data.BuildingInfo[i].BuildingID);
            tgtBldgIds.push(tgtBldgId);//すべての建物が選択できる。
        }

        //data定義
        const state = reactive<{selectBldgids: Array<string>, count: number}>({
            selectBldgids: [],//(2)選択した建物ID（選択表示と、別コンポーネントへ伝達）
            count: 0
        });
        
        //method定義
        const increment = () =>{
            state.count = state.count + 1;
            context.emit('add', state.count);
        }

        onMounted(() => {
            //mapboxのアクセストークンをセット
            mapboxgl.accessToken = 'pk.eyJ1Ijoia2F0b3NodW4iLCJhIjoiY2t0eWVjN3dvMDlzbjJub3c2YndiZTJvNiJ9.GbIeJ78HgWKX6sh2mKy2pA';
            
            //mapを定義
            const map = new mapboxgl.Map({
                container: 'map',
                style: 'mapbox://styles/mapbox/light-v10',
                center: setting.map_center,
                zoom: 17,
                maxZoom: 21,
                antialias: true, // create the gl context with MSAA antialiasing, so custom layers are antialiased
            });

            //非表示にする建物のIDのリスト
            var hideBldgIds:number[] = setting.hideBldgIds;
            //bldgListの建物IDを非表示対象に追加する。
            for(var i in bldgList.data){
                var bldg = bldgList.data[i];
                //配列に追加
                hideBldgIds = hideBldgIds.concat(bldg.bldgIds);
            }

            interface Pointlike {
                x: number;
                y: number;
            }

            var firstFlg = true

            //監視関数の設定
            const {highlightBldgIds} = toRefs(props);
            // const {tgtBldgIds} = toRefs(props);
            watch(highlightBldgIds, () =>{
                //ダミー処理定義（ToDo：map.setFilterの処理を関数化し、ここから呼ぶように集約する）
                //alert("[変更検知]ダミー処理：Map.vue:highlightBldgIds=" + props.highlightBldgIds)
            });
            effect(() => {
                //ダミー処理定義（ToDo：map.setFilterの処理を関数化し、ここから呼ぶように集約する）
                // alert("[変更検知]ダミー処理：Map.vue:highlightBldgIds=" + props.highlightBldgIds)
                // alert("[変更検知]ダミー処理：Map.vue:selectBldgids=" + state.selectBldgids)
                // この機能が動けるため「dummy」の変数を「props.highlightBldgIds」の値を持たせた
                var dummy = props.highlightBldgIds;
                UpdateHighlightBuildings()
            })

            //イベントの追加（建物ボリュームの読み込み）
            map.on('load', () => {
                // Insert the layer beneath any symbol layer.
                //let layers = map.getStyle().getLaysers();
                /*
                let layers = map.getStyle()['layers'];//11/15 ←のエラーが消えない
                //const styletest = map.getStyle();
                let labelLayerId = '';
                if(layers != undefined){
                    labelLayerId = layers.find(
                    (layer) => layer.type === 'symbol' && layer.layout['text-field']
                    ).id;
                }
                */
               const labelLayerId = 'road-label';
                
                //レイヤー１：親コンポーネントからセットされたハイライト建物//
                map.addLayer({
                    'id': 'main-buildings',
                    'source': 'composite',
                    'source-layer': 'building',
                    'filter': [
                        'all',
                        ['==', 'extrude', 'true'],
                        ['>', 'height', 0],
                        ['!in', '$id', ...hideBldgIds], //非表示建物は表示しない。
                        ['in', '$id', ...(props.highlightBldgIds as Array<number>)] //ハイライト建物を対象にする。
                    ],
                    'type': 'fill-extrusion',
                    'minzoom': 15,
                    'paint': {
                        'fill-extrusion-color': setting.color.highlightBuildings,
                        'fill-extrusion-height': ["number", ["get", "height"], 5],
                        'fill-extrusion-base': ["number", ["get", "min_height"], 0],
                        'fill-extrusion-opacity': 0.6
                    }
                },
                labelLayerId);

                //レイヤー２：親コンポーネントからセットされたハイライト建物（選択状態）//
                map.addLayer({	
                    "id": "selected-main-building",
                    "source": "composite",
                    'source-layer': 'building',
                    'type': 'fill-extrusion',
                    'paint': {
                        'fill-extrusion-color': setting.color.selectedBuilding,
                        'fill-extrusion-height': ["number", ["get", "height"], 5],
                        'fill-extrusion-base': ["number", ["get", "min_height"], 0],
                        'fill-extrusion-opacity': 0.6
                    },
                    'filter': ['in', '$id', '']
                }, labelLayerId);

                //レイヤー３：ハイライト建物以外の周辺建物//
                map.addLayer({
                    'id': 'other-buildings',
                    'source': 'composite',
                    'source-layer': 'building',
                    'filter': [
                        'all',
                        ['==', 'extrude', 'true'],
                        ['>', 'height', 0],
                        ['!in', '$id', ...hideBldgIds],
                        ['!in', '$id', ...(props.highlightBldgIds as Array<number>)], //ハイライト建物を対象にする。
                        ['in', '$id', ...(tgtBldgIds as Array<number>)]
                    ],
                    'type': 'fill-extrusion',
                    'minzoom': 15,
                    'paint': {
                        'fill-extrusion-color': setting.color.relatedBuildings,
                        'fill-extrusion-height': ["number", ["get", "height"], 5],
                        'fill-extrusion-base': ["number", ["get", "min_height"], 0],
                        'fill-extrusion-opacity': 0.6
                    }
                },
                labelLayerId);

                //レイヤー４：ハイライト建物以外の周辺建物（選択状態）//
                map.addLayer({	
                    "id": "selected-other-building",
                    "source": "composite",
                    'source-layer': 'building',
                    'type': 'fill-extrusion',
                    'paint': {
                        'fill-extrusion-color': setting.color.selectedBuilding,
                        'fill-extrusion-height': ["number", ["get", "height"], 5],
                        'fill-extrusion-base': ["number", ["get", "min_height"], 0],
                        'fill-extrusion-opacity': 0.6
                    },
                    'filter': ['in', '$id', '']
                }, labelLayerId);

                //レイヤー5：ハイライト建物以外の周辺建物（選択状態）//
                map.addLayer({	
                    "id": "not-related-building",
                    "source": "composite",
                    'source-layer': 'building',
                    'type': 'fill-extrusion',
                    'filter': [
                        'all',
                        ['==', 'extrude', 'true'],
                        ['>', 'height', 0],
                        ['!in', '$id', ...hideBldgIds],
                        ['!in', '$id', ...(props.highlightBldgIds as Array<number>)], //ハイライト建物を対象にする。
                        ['!in', '$id', ...(tgtBldgIds as Array<number>)]
                    ],
                    'paint': {
                        'fill-extrusion-color': setting.color.notRelatedBuilding,
                        'fill-extrusion-height': ["number", ["get", "height"], 5],
                        'fill-extrusion-base': ["number", ["get", "min_height"], 0],
                        'fill-extrusion-opacity': 0.6
                    },
                }, labelLayerId);

                //金魚の表示------------------start
                //(1)glTFを読み込む
                    //参考HP
                    //https://docs.mapbox.com/jp/mapbox-gl-js/example/add-3d-model/
                    // parameters to ensure the model is georeferenced correctly on the map
                    const modelOrigin:mapboxgl.LngLatLike = floatList.modelOrigin;
                    const modelAltitude = floatList.modelAltitude;
                    const modelRotate = [
                        0 * (Math.PI / 2), 
                        0 * (Math.PI / 2), 
                        0 * (Math.PI / 2)
                    ];

                    const modelAsMercatorCoordinate = mapboxgl.MercatorCoordinate.fromLngLat(
                        modelOrigin,
                        modelAltitude
                    );

                    // transformation parameters to position, rotate and scale the 3D model onto the map
                    const modelTransform = {
                        translateX: modelAsMercatorCoordinate.x,
                        translateY: modelAsMercatorCoordinate.y,
                        translateZ: modelAsMercatorCoordinate.z,
                        rotateX: modelRotate[0],
                        rotateY: modelRotate[1],
                        rotateZ: modelRotate[2],
                        /* Since the 3D model is in real world meters, a scale transform needs to be
                        * applied since the CustomLayerInterface expects units in MercatorCoordinates.
                        */
                        scale: modelAsMercatorCoordinate.meterInMercatorCoordinateUnits()
                    };

                    //コンポーネントをimportしているので不要と判断
                    //const THREE = window.THREE;

                    //customLayerで用いるmapbox,three.jsのオブジェクト(ToDoオブジェクト定義を修正)
                    /*
                    var customLayerInfo = {
                        camera : new THREE.Camera(),
                        scene : new THREE.Scene(),
                        map : new mapboxgl.Map(),
                        renderer : new THREE.WebGLRenderer(),
                    }*/
                    var self_camera:THREE.Camera;
                    var self_scene:THREE.Scene;
                    var self_map:mapboxgl.Map;
                    var self_renderer:THREE.WebGLRenderer;
                    //金魚オブジェクト
                    var arrayKingyoObj:Array<THREE.Object3D> = [];

                    // configuration of the custom layer for a 3D model per the CustomLayerInterface
                    const customLayer:mapboxgl.AnyLayer = {
                        id: 'kingyo',
                        type: 'custom',
                        renderingMode: '3d',

                        onAdd: function (map:mapboxgl.Map, gl:WebGLRenderingContext) {
                            //customLayerInfoの変数に集約
                            //this.camera = new THREE.Camera();
                            //this.scene = new THREE.Scene();
                            self_camera = new THREE.Camera();
                            self_scene = new THREE.Scene();

                            // create two three.js lights to illuminate the model
                            const directionalLight = new THREE.DirectionalLight(0xffffff);
                            directionalLight.position.set(0, -70, 100).normalize();
                            //customLayerInfoの変数に集約
                            //this.scene.add(directionalLight);
                            self_scene.add(directionalLight);

                            const directionalLight2 = new THREE.DirectionalLight(0xffffff);
                            directionalLight2.position.set(0, 70, 100).normalize();
                            //customLayerInfoの変数に集約
                            //this.scene.add(directionalLight2);
                            self_scene.add(directionalLight2);

                            /* GLTFLoaderからobjLoaderに変更

                            // use the three.js GLTF loader to add the 3D model to the three.js scene
                            //const loader = new THREE.GLTFLoader();
                            const loader = new GLTFLoader();//inportしているコンポーネントをそのまま使う。
                            loader.load(
                                //'https://docs.mapbox.com/mapbox-gl-js/assets/34M_17/34M_17.gltf',
                                //'http://127.0.0.1:5000/asset/34M_17.gltf',
                                'http://localhost:8080/assets/antenna/34M_17.gltf',
                                (gltf) => {
                                    //customLayerInfoの変数に集約
                                    //this.scene.add(gltf.scene);
                                    self_scene.add(gltf.scene);
                                }
                            );
                            */

                            //金魚のobjのload------start
                            // model

                            var onProgress = function ( e:ProgressEvent ) {
                                if ( e.lengthComputable ) {
                                    var percentComplete = e.loaded / e.total * 100;
                                    //console.log( Math.round(percentComplete, 2) + '% downloaded' );
                                    console.log( percentComplete + '% downloaded' );
                                }
                            };

                            /*var onError = function ( event:ErrorEvent ):void { 
                                //console.error('obj load error!');
                            };*/
                            var onError = function ( xhr:any ) { 
                                console.error('obj loader error!');
                            };

                            /**
                             * 金魚オブジェクトをobjファイルからロード
                             * {String} folderPath obj・mtlファイルのフォルダのパス
                             * {String} mtlPath mtlファイルのパス
                             * {String} objPath objファイルのパス
                             * {THREE.Vector3} pos オブジェクトの相対座標
                             * {number} scale オブジェクトのスケール補正
                             */
                            function loadKingyoObjFromObj(folderPath:string,mtlPath:string, objPath:string, pos:THREE.Vector3, scaleNum:number) {
                                mtlLoader.setPath( folderPath );
                                mtlLoader.load( mtlPath, function( materials ) {

                                    materials.preload();

                                    //var objLoader = new THREE.OBJLoader();
                                    //var objLoader = new OBJLoader();
                                    objLoader.setMaterials( materials );
                                    objLoader.setPath( folderPath );
                                    //objLoader.load( 'male02.obj', function ( object ) {
                                    objLoader.load( objPath, function ( object:THREE.Object3D ) {

                                        //座標を設定
                                        object.position.x = pos.x;
                                        object.position.y = pos.y;
                                        object.position.z = pos.z;
                                        //縮尺を設定
                                        object.scale.x = scaleNum;
                                        object.scale.y = scaleNum;
                                        object.scale.z = scaleNum;

                                        self_scene.add( object );

                                        //金魚オブジェクトを保持
                                        arrayKingyoObj.push(object);

                                    }, onProgress, onError );

                                });
                            }

                            /**
                             * 金魚オブジェクトをfbxファイルからロード
                             * {String} folderPath fbxファイルのフォルダのパス
                             * {String} fbxPath fbxファイルのパス
                             * {THREE.Vector3} pos オブジェクトの相対座標
                             * {number} scale オブジェクトのスケール補正
                             */
                            function loadKingyoFbxFromObj(folderPath:string, fbxPath:string, pos:THREE.Vector3, scaleNum:number) {
                                fbxLoader.setPath( folderPath );
                                fbxLoader.load( fbxPath, function ( object:THREE.Object3D ) {

                                    object.position.x = pos.x ;
                                    object.position.y = pos.y ;
                                    object.position.z = pos.z ;
                                    //縮尺
                                    object.scale.x = scaleNum;
                                    object.scale.y = scaleNum;
                                    object.scale.z = scaleNum;

                                    //scene.add( object );
                                    self_scene.add( object );

                                    //金魚オブジェクトを保持
                                    arrayKingyoObj.push(object);

                                }, onProgress, onError );
                            }

                            //ローダーを生成
                            var mtlLoader = new MTLLoader();
                            var objLoader = new OBJLoader();
                            var fbxLoader = new FBXLoader();

                            //金魚を読み込み（floatList.jsonから）
                            for(var i in floatList.data){
                                var float = floatList.data[i];
                                if(float.type == "obj"){//objの場合
                                    loadKingyoObjFromObj(float.folderPath, (float.mtlPath as string), float.filePath, new THREE.Vector3(float.pos.x,float.pos.y,float.pos.z), float.scaleNum);
                                }else if(float.type == "fbx"){//fbxの場合
                                    loadKingyoFbxFromObj(float.folderPath, float.filePath, new THREE.Vector3(float.pos.x,float.pos.y,float.pos.z), float.scaleNum);
                                }else{
                                    alert('floatList.jsonに対象外のタイプのファイル定義があります');
                                }
                            }

                            //金魚のobjのload------end


                            //customLayerInfoの変数に集約
                            //this.map = map;
                            self_map = map;

                            // use the Mapbox GL JS map canvas for three.js
                            //customLayerInfoの変数に集約
                            //this.renderer = new THREE.WebGLRenderer({
                            self_renderer = new THREE.WebGLRenderer({
                                canvas: map.getCanvas(),
                                context: gl,
                                antialias: true
                            });

                            //customLayerInfoの変数に集約
                            //this.renderer.autoClear = false;
                            self_renderer.autoClear = false;
                        },
                        render: function (gl:WebGLRenderingContext, matrix:number[]) {
                            const rotationX = new THREE.Matrix4().makeRotationAxis(
                                new THREE.Vector3(1, 0, 0),
                                modelTransform.rotateX
                            );
                            const rotationY = new THREE.Matrix4().makeRotationAxis(
                                new THREE.Vector3(0, 1, 0),
                                modelTransform.rotateY
                            );
                            const rotationZ = new THREE.Matrix4().makeRotationAxis(
                                new THREE.Vector3(0, 0, 1),
                                modelTransform.rotateZ
                            );

                            const m = new THREE.Matrix4().fromArray(matrix);
                            const l = new THREE.Matrix4()
                                .makeTranslation(
                                    modelTransform.translateX,
                                    modelTransform.translateY,
                                    modelTransform.translateZ as number
                                )
                                .scale(
                                    new THREE.Vector3(
                                        modelTransform.scale,
                                        -modelTransform.scale,
                                        modelTransform.scale
                                    )
                                )
                                .multiply(rotationX)
                                .multiply(rotationY)
                                .multiply(rotationZ);

                            //customLayerInfoの変数に集約
                            //this.camera.projectionMatrix = m.multiply(l);
                            //this.renderer.resetState();
                            //this.renderer.render(this.scene, this.camera);
                            //this.map.triggerRepaint();
                            self_camera.projectionMatrix = m.multiply(l);
                            self_renderer.resetState();

                            //金魚の動作にアニメーションをつける-------start
                            // 次のフレームを要求
                            //requestAnimationFrame(() => { this.render(); });
                            requestAnimationFrame(() => { self_renderer; });

                            // ちょっとずつ移動させる
                            arrayKingyoObj.forEach(elm => {
                                updateKingyoPos(elm);
                            });

                            //金魚の動作にアニメーションをつける-------end

                            self_renderer.render(self_scene, self_camera);
                            self_map.triggerRepaint();
                        }
                    };

                    /** 
                     * 金魚をアニメーション動作する。
                     * kingyoObj 更新対象のオブジェクト
                    */
                    function updateKingyoPos(kingyoObj:THREE.Object3D):void {
                        //時間で動きを変える
                        var date = new Date();//今日の日時を設定
                        
                        //1分単位で往復するようにする。
                        var switchBackFlg = false;
                        if(Math.round(date.getMinutes()) % 2 === 0){
                            switchBackFlg = true;//偶数の場合はスイッチバックする。
                        }

                        //制御定数//
                        //移動
                        const confPosX = 2.0;
                        const confPosY = 0.5;
                        const confPosZ = 1.0;
                        //回転
                        const confRotX = 2.0;
                        const confRotY = 0.5;
                        const confRotZ = 1.0;
                        
                        //ランダムに動作する//
                        //回転//
                        //kingyoObj.rotation.x += confRotX * (0.5 - Math.random());
                        //kingyoObj.rotation.y += confRotY * (0.5 - Math.random());
                        //kingyoObj.rotation.z += confRotZ * (0.5 - Math.random());
                        //振り向かせる
                        if(!switchBackFlg){
                            kingyoObj.rotation.z = 0;
                        }else{
                            kingyoObj.rotation.z = 2 * (Math.PI / 2);//反転
                        }

                        //移動//
                        //振り返るか判断
                        var switchBackVal = 1;
                        if(switchBackFlg){
                            switchBackVal = -1;
                        }
                        kingyoObj.position.x += switchBackVal * confPosX * (0.7 - Math.random());//進行方向を反転
                        kingyoObj.position.z += confPosZ * (0.5 - Math.random());
                    }

                    //map.on('style.load', () => {
                    //map.on('load', () => {
                        map.addLayer(customLayer, 'waterway-label');
                        //map.addLayer(customLayer, labelLayerId);
                    //});


                //金魚の表示------------------end

                //レイヤー６（建物のgltfの表示）
                /**
                 * 建物のgltfを読み込んだレイヤーを生成
                 * {string} fileType 読み込むファイル形式（ローダーを変える）
                 * {mapboxgl.LngLatLike} modelOrigin モデルを配置する緯度経度
                 * {string} layerName レイヤー名称
                 * {string} folderPath フォルダのパス(スラッシュまでいれる)
                 * {string} gltfFileName gltfのファイル名
                 * {} scale 縮尺
                 */
                function createBldgLayer(
                        fileType:string,
                        modelOrigin:mapboxgl.LngLatLike,
                        layerName:string,
                        folderPath:string,
                        fileName:string,
                        scale:THREE.Vector3,
                        rotDeg:number
                    ) :void{
                    
                    //gltf,glb,fbx以外はアラートして処理しない
                    if(!(fileType == "gltf" || fileType == "glb" || fileType == "fbx")){
                        alert('createBldgLayer：読み込み対象外のファイル形式です：' + fileType);
                        return;
                    }

                    //(1)glTFを読み込む
                    //参考HP
                    //https://docs.mapbox.com/jp/mapbox-gl-js/example/add-3d-model/
                    // parameters to ensure the model is georeferenced correctly on the map
                    const modelAltitude = 0;
                    const modelRotate = [
                        1 * (Math.PI / 2), 
                        0 * (Math.PI / 2), 
                        0 * (Math.PI / 2)
                    ];

                    const modelAsMercatorCoordinate = mapboxgl.MercatorCoordinate.fromLngLat(
                        modelOrigin,
                        modelAltitude
                    );

                    // transformation parameters to position, rotate and scale the 3D model onto the map
                    const modelTransform = {
                        translateX: modelAsMercatorCoordinate.x,
                        translateY: modelAsMercatorCoordinate.y,
                        translateZ: modelAsMercatorCoordinate.z,
                        rotateX: modelRotate[0],
                        rotateY: modelRotate[1],
                        rotateZ: modelRotate[2],
                        /* Since the 3D model is in real world meters, a scale transform needs to be
                        * applied since the CustomLayerInterface expects units in MercatorCoordinates.
                        */
                        scale: modelAsMercatorCoordinate.meterInMercatorCoordinateUnits()
                    };

                    var self_camera:THREE.Camera;
                    var self_scene:THREE.Scene;
                    var self_map:mapboxgl.Map;
                    var self_renderer:THREE.WebGLRenderer;
 
                    // configuration of the custom layer for a 3D model per the CustomLayerInterface
                    const customLayer:mapboxgl.AnyLayer = {
                        id: layerName,
                        type: 'custom',
                        renderingMode: '3d',

                        onAdd: function (map:mapboxgl.Map, gl:WebGLRenderingContext) {
                            //customLayerInfoの変数に集約
                            self_camera = new THREE.Camera();
                            self_scene = new THREE.Scene();

                            // create two three.js lights to illuminate the model
                            const directionalLight = new THREE.DirectionalLight(0xffffff);
                            directionalLight.position.set(0, -70, 100).normalize();
                            //customLayerInfoの変数に集約
                            self_scene.add(directionalLight);

                            const directionalLight2 = new THREE.DirectionalLight(0xffffff);
                            directionalLight2.position.set(0, 70, 100).normalize();
                            //customLayerInfoの変数に集約
                            self_scene.add(directionalLight2);

                            //ローダーで使うエラー関数//
                            var onProgress = function ( e:ProgressEvent ) {
                                if ( e.lengthComputable ) {
                                    var percentComplete = e.loaded / e.total * 100;
                                    //console.log( Math.round(percentComplete, 2) + '% downloaded' );
                                    console.log( percentComplete + '% downloaded' );
                                }
                            };

                            /*var onError = function ( event:ErrorEvent ):void { 
                                //console.error('obj load error!');
                            };*/
                            var onError = function ( xhr:any ) { 
                                //console.error('obj loader error!');
                                alert(fileType + ' loader error:' + xhr);
                            };

                            if(fileType == "gltf" || fileType == "glb"){
                                //glbローダーで読み込む
                                // use the three.js GLTF loader to add the 3D model to the three.js scene
                                const loader = new GLTFLoader();//inportしているコンポーネントをそのまま使う。
                                loader.load(
                                    (folderPath + fileName),//GLTFのファイルパス
                                    (gltf) => {
                                        //回転(鉛直方向の時計回りにrotDeg回転)
                                        gltf.scene.rotation.y = (rotDeg / 90) * (Math.PI / 2),
                                        //縮尺
                                        gltf.scene.scale.x = scale.x;
                                        gltf.scene.scale.y = scale.y;
                                        gltf.scene.scale.z = scale.z;
                                        self_scene.add(gltf.scene);
                                    }, 
                                    onProgress, onError
                                );
                            }else if(fileType == "fbx"){
                                //fbxローダーで読み込む
                                const loader = new FBXLoader();//inportしているコンポーネントをそのまま使う。
                                loader.setPath( folderPath );
                                loader.load( fileName, 
                                            function ( object:THREE.Object3D ) {
                                                //回転(鉛直方向の時計回りにrotDeg回転)
                                                object.rotation.y = (rotDeg / 90) * (Math.PI / 2),
                                                //縮尺
                                                object.scale.x = scale.x;
                                                object.scale.y = scale.y;
                                                object.scale.z = scale.z;
                                                self_scene.add( object );
                                            }, 
                                            onProgress, onError 
                                );
                            }

                            self_map = map;

                            // use the Mapbox GL JS map canvas for three.js
                            self_renderer = new THREE.WebGLRenderer({
                                canvas: map.getCanvas(),
                                context: gl,
                                antialias: true
                            });

                            self_renderer.autoClear = false;
                        },
                        render: function (gl:WebGLRenderingContext, matrix:number[]) {
                            const rotationX = new THREE.Matrix4().makeRotationAxis(
                                new THREE.Vector3(1, 0, 0),
                                modelTransform.rotateX
                            );
                            const rotationY = new THREE.Matrix4().makeRotationAxis(
                                new THREE.Vector3(0, 1, 0),
                                modelTransform.rotateY
                            );
                            const rotationZ = new THREE.Matrix4().makeRotationAxis(
                                new THREE.Vector3(0, 0, 1),
                                modelTransform.rotateZ
                            );

                            const m = new THREE.Matrix4().fromArray(matrix);
                            const l = new THREE.Matrix4()
                                .makeTranslation(
                                    modelTransform.translateX,
                                    modelTransform.translateY,
                                    modelTransform.translateZ as number
                                )
                                .scale(
                                    new THREE.Vector3(
                                        modelTransform.scale,
                                        -modelTransform.scale,
                                        modelTransform.scale
                                    )
                                )
                                .multiply(rotationX)
                                .multiply(rotationY)
                                .multiply(rotationZ);

                            self_camera.projectionMatrix = m.multiply(l);
                            self_renderer.resetState();

                            self_renderer.render(self_scene, self_camera);
                            self_map.triggerRepaint();
                        }
                    };

                    map.addLayer(customLayer, 'waterway-label');
                }
                //GLTFのレイヤーを生成(bldgList.jsonを読み込む)
                for(var i in bldgList.data){
                    var bldg = bldgList.data[i];
                    if(bldg.type == "gltf" || bldg.type == "glb"|| bldg.type == "fbx"){//gltfかfbxの場合
                        createBldgLayer(bldg.type,bldg.lnglat, bldg.layerName, bldg.folderPath, bldg.filePath, 
                                        new THREE.Vector3(bldg.scale.x, bldg.scale.y, bldg.scale.z), bldg.rotDeg );
                    }else{
                        //alert('bldgList.jsonに対象外のタイプのファイル定義があります');
                    }
                }

                //建物ボリュームを追加（geo.jsonで定義）
                function createGeojsonBldgLayer():void{
                    map.addSource('floorplan', {
                        'type': 'geojson',
                        /*
                        * Each feature in this GeoJSON file contains values for
                        * `properties.height`, `properties.base_height`,
                        * and `properties.color`.
                        * In `addLayer` you will use expressions to set the new
                        * layer's paint properties based on these values.
                        */
                        'data': setting.geojsonPath //publicフォルダの公開ファイルを読み込み
                    });
                    map.addLayer({
                        'id': 'room-extrusion',
                        'type': 'fill-extrusion',
                        'source': 'floorplan',
                        'paint': {
                        // Get the `fill-extrusion-color` from the source `color` property.
                        'fill-extrusion-color': ['get', 'color'],
                        
                        // Get `fill-extrusion-height` from the source `height` property.
                        'fill-extrusion-height': ['get', 'height'],
                        
                        // Get `fill-extrusion-base` from the source `base_height` property.
                        'fill-extrusion-base': ['get', 'base_height'],
                        
                        // Make extrusions slightly opaque to see through indoor walls.
                        'fill-extrusion-opacity': 0.5
                        }
                    });
                }
                if(setting.isCreateGeojsonBldgLayer){
                    //設定によって呼ぶ場合と呼ばない場合を制御
                    createGeojsonBldgLayer();
                }

            });

            
            //トライアル：建物の選択
            //参考HP
            //https://docs.mapbox.com/mapbox-gl-js/example/queryrenderedfeatures-around-point/

            //'other-buildings'の更新//
            map.on('click', (e: mapboxgl.MapMouseEvent & mapboxgl.EventData) => {
                // Set `bbox` as 5px reactangle area around clicked point.
                const bbox:[mapboxgl.Point, mapboxgl.Point] = [
                    new mapboxgl.Point(e.point.x - 5, e.point.y - 5),
                    new mapboxgl.Point(e.point.x + 5, e.point.y + 5)
                ];
                /*
                if(!(bbox instanceof number[][])){
                    return;//コンパイルエラー回避
                }*/

                // Find features intersecting the bounding box.
                const selectedFeatures = map.queryRenderedFeatures(bbox, {
                layers: ['other-buildings']
                });
                const ids = selectedFeatures.map(
                    (feature) => feature.id
                );

                //1件以上選択できた場合のみ、親コンポーネントにIDを通知（main-buildingsレイヤーとother-buildingsレイヤーが両方動作しないように）
                if(ids.length > 0){
                    let tempSelectBldgids:Array<string> = [];//選択した建物IDを一時的に格納
                    ids.forEach((value,idx)=>{
                        if(value == undefined){
                            return;
                        }
                        let tmpBldgId:string;
                        if( (typeof value) == "string" ){
                            //stringの場合
                            tmpBldgId = value.toString();
                        }else{
                            tmpBldgId = String(value); 
                        } 
                        tempSelectBldgids.push(tmpBldgId);//リアクティブのイベントが１回だけ呼ばれるように、代入を１回にする。
                    });
                    state.selectBldgids = tempSelectBldgids;
                    context.emit('uploadBldgId', state.selectBldgids);
                }

                //選択した建物に色付け(周辺建物)//
                map.setFilter('other-buildings',
                [
                    'all',
                    ['==', 'extrude', 'true'],
                    ['>', 'height', 0],
                    ['!in', '$id', ...hideBldgIds], //非表示建物は表示しない。
                    ['!in', '$id', ...(props.highlightBldgIds as Array<number>)], //ハイライト建物を対象にする。
                    ['in', '$id', ...(tgtBldgIds as Array<number>)],
                    ['!in', '$id', ...ids]//選択したもの
                ]
                ,
                );
                map.setFilter('selected-other-building',
                    ['in', '$id', ...ids]
                );
            });

            //'main-buildings'の更新//
            map.on('click', (e: mapboxgl.MapMouseEvent & mapboxgl.EventData) => {
                // Set `bbox` as 5px reactangle area around clicked point.
                const bbox:[mapboxgl.Point, mapboxgl.Point] = [
                    new mapboxgl.Point(e.point.x - 5, e.point.y - 5),
                    new mapboxgl.Point(e.point.x + 5, e.point.y + 5)
                ];

                // Find features intersecting the bounding box.
                const selectedFeatures = map.queryRenderedFeatures(bbox, {
                layers: ['main-buildings']
                });
                const ids = selectedFeatures.map(
                    (feature) => feature.id
                );

                //1件以上選択できた場合のみ、親コンポーネントにIDを通知（main-buildingsレイヤーとother-buildingsレイヤーが両方動作しないように）
                if(ids.length > 0){
                    let tempSelectBldgids:Array<string> = [];//選択した建物IDを一時的に格納
                    ids.forEach((value,idx)=>{
                        if(value == undefined){
                            return;
                        }
                        let tmpBldgId:string;
                        if( (typeof value) == "string" ){
                            //stringの場合
                            tmpBldgId = value.toString();
                        }else{
                            tmpBldgId = String(value); 
                        } 
                        tempSelectBldgids.push(tmpBldgId);//リアクティブのイベントが１回だけ呼ばれるように、代入を１回にする。
                    });
                    state.selectBldgids = tempSelectBldgids;
                    context.emit('uploadBldgId', state.selectBldgids);
                }

                //選択した建物に色付け(ハイライト建物)//
                map.setFilter('main-buildings',
                [
                    'all',
                    ['==', 'extrude', 'true'],
                    ['>', 'height', 0],
                    ['!in', '$id', ...hideBldgIds], //非表示建物は表示しない。
                    ['in', '$id', ...(props.highlightBldgIds as Array<number>)], //ハイライト建物を対象にする。
                    ['!in', '$id', ...ids]//選択したもの
                ]
                ,
                );
                map.setFilter('selected-main-building',
                    ['in', '$id', ...ids]
                );
            });

            //建物IDを確認するためのデバッグ用の関数
            if(debug){
                //イベントハンドルを実装
                map.on('click', (e: mapboxgl.MapMouseEvent & mapboxgl.EventData) => {
                    // Set `bbox` as 5px reactangle area around clicked point.
                    const bbox:[mapboxgl.Point, mapboxgl.Point] = [
                        new mapboxgl.Point(e.point.x - 5, e.point.y - 5),
                        new mapboxgl.Point(e.point.x + 5, e.point.y + 5)
                    ];

                    // Find features intersecting the bounding box.
                    const selectedFeatures = map.queryRenderedFeatures(bbox, {
                        layers: ['not-related-building']
                    });
                    const ids = selectedFeatures.map(
                        (feature) => feature.id
                    );

                    //1件以上選択できた場合のみ、親コンポーネントにIDを通知（main-buildingsレイヤーとother-buildingsレイヤーが両方動作しないように）
                    if(ids.length > 0){
                        alert('建物情報がない建物が選択されました。（選択した建物のID：' + ids);
                    }
                });
            }
                
            firstFlg = false

            function UpdateHighlightBuildings(){
                if (!firstFlg){
                    map.setFilter('main-buildings', [
                        'all',
                        ['==', 'extrude', 'true'],
                        ['>', 'height', 0],
                        ['!in', '$id', ...hideBldgIds], //非表示建物は表示しない。（KKE新館部分）
                        ['in', '$id', ...(props.highlightBldgIds as Array<number>)] //ハイライト建物を対象にする。
                    ]);
                    map.setFilter('other-buildings',  [
                        'all',
                        ['==', 'extrude', 'true'],
                        ['>', 'height', 0],
                        ['!in', '$id', ...hideBldgIds],
                        ['!in', '$id', ...(props.highlightBldgIds as Array<number>)], //ハイライト建物を対象にする。
                        ['in', '$id', ...(tgtBldgIds as Array<number>)]
                    ]);
                }
            }
        });

        return{
            state,
            increment
        }
    },
});
