<template>
    <div>
        <h1>◆開店店舗表示</h1>
        入力した時間帯にずっと空いている店舗が分かります。
        <ul>
            <select name="startTime" @change="GetStartTime($event)">
                <option value="-" selected>--:--</option>
                <option value="1700">17:00</option>
                <option value="1800">18:00</option>
                <option value="1900">19:00</option>
                <option value="2000">20:00</option>
                <option value="2100">21:00</option>
                <option value="2200">22:00</option>
                <option value="2300">23:00</option>
                <option value="2400">24:00</option>
            </select>
            ～
            <select name="endTime" @change="GetEndTime($event)">
                <option value="-" selected>--:--</option>
                <option value="1800">18:00</option>
                <option value="1900">19:00</option>
                <option value="2000">20:00</option>
                <option value="2100">21:00</option>
                <option value="2200">22:00</option>
                <option value="2300">23:00</option>
                <option value="2400">24:00</option>
            </select>
        </ul>
    </div>
</template>map
<script>
import data from '../assets/TestData.json';
import { defineComponent, toRefs } from 'vue';

export default defineComponent({
    emits:['openBldgId'],
    name:"Test",
        props: {
        //(1)ハイライト表示する建物のIDのリストが渡ってくる
        highlightBldgIds:Array,
        startTime: String,
        endTime: String
    },

    setup(props, context) {
        var {startTime} = toRefs(props);
        startTime = "-";
        var {endTime} = toRefs(props);
        endTime = "-";

        function GetStartTime(e){
            startTime = e.target.value;
            if (endTime != "-"){
                TestFunction();
            }
        }

        function GetEndTime(e){
            endTime = e.target.value;
            if (startTime != "-"){
                TestFunction();
            }
        }

        function TestFunction(){
                const highlightBldgIds = [];
                for (const i in data.BuildingInfo){
                    if (parseInt(data.BuildingInfo[i].Space.BusinessStartTime) <= parseInt(startTime) 
                        && parseInt(data.BuildingInfo[i].Space.BusinessEndTime) >= parseInt(endTime)){
                        var buildingId = parseInt(data.BuildingInfo[i].BuildingID);
                        highlightBldgIds.push(buildingId);
                    }
                context.emit('openBldgId', highlightBldgIds);
                }
            }

        return{
            GetStartTime,
            GetEndTime,
            TestFunction
        }
    }
});
</script>

<style scoped>
div{
    border: 1pt solid orange
}
h1{
    color: orange
}
p{
    color: orange
}
</style>


