

import { Chart, ChartData, registerables } from "chart.js";
import { defineComponent, effect, reactive } from "vue";
import { BarChart } from "vue-chart-3";
import data from '../assets/TestData.json';

let Timedat:Array<string>=[];
let ppldat:Array<number>=[];

// 使用するモジュールの宣言
// ここでは便宜上、全てのモジュールを使用する registerables を使用しているが、
// 必要十分なモジュールのみを宣言することで、ツリーシェイキングを行うことができる
Chart.register(...registerables);

export default defineComponent({
  components: {
    BarChart
  },
  //パラメータ（親コンポーネントから渡るもの）
  props: {
      //(1)選択した建物のIDのリストが渡ってくる
      selectBldgids:Array
  },

//created is called at the begining of program. OnStartup()と同じ
  created(){
      //特になにもしない。
    },
  setup(props, context) {    
    //data定義
    const state = reactive<{barData: ChartData<"bar">, bldgName:string, bldgAddress:string, bldgInfoUrl:string}>({
        barData: {
          labels:[], //use time data from array
          datasets:[
              {
                  label:"来店人数",
                  data:[], //use people data from array
                  backgroundColor: ['rgba(54, 162, 235)'],
              },
          ],
        },//グラフのデータ
            //表示する建物情報
        bldgName : "",
        bldgAddress : "",
        bldgInfoUrl : ""
    });

    //propの建物IDが変更されたら、barDataを更新
    effect(() => {
        if( (props.selectBldgids as Array<number>).length > 0) {
           state.barData = createBarChartData( (props.selectBldgids as Array<number>)[0] );
           state.bldgName = getBldgName( (props.selectBldgids as Array<number>)[0] );
           state.bldgAddress = getBldgAddress( (props.selectBldgids as Array<number>)[0] );
           state.bldgInfoUrl = getBldgInfoUrl( (props.selectBldgids as Array<number>)[0] );
        }
    })

    //建物名を生成する関数
    function getBldgName(buildingId:number):string {
        //データを更新
        for(var i in data.BuildingInfo){
          var bldg = data.BuildingInfo[i]
          if(bldg.BuildingID != String(buildingId)){
            //building idで絞り込む
            continue;//対象以外のデータは、スキップする
          }
          return bldg.Name;
        }
        return "notFound";
    }

    //住所を生成する関数
    function getBldgAddress(buildingId:number):string {
        //データを更新
        for(var i in data.BuildingInfo){
          var bldg = data.BuildingInfo[i]
          if(bldg.BuildingID != String(buildingId)){
            //building idで絞り込む
            continue;//対象以外のデータは、スキップする
          }
          return bldg.Address;
        }
        return "notFound";
    }

    //建物のホームページのURLを生成する関数
    function getBldgInfoUrl(buildingId:number):string {
        //データを更新
        for(var i in data.BuildingInfo){
          var bldg = data.BuildingInfo[i]
          if(bldg.BuildingID != String(buildingId)){
            //building idで絞り込む
            continue;//対象以外のデータは、スキップする
          }
          return bldg.InfoUrl;
        }
        return "notFound";
    }

    //bar dataを生成する関数
    function createBarChartData(buildingId:number):ChartData<"bar"> {
      //グラフの値を更新
      {
        //初期化
        Timedat =[];
        ppldat = [];
        
        //データを更新
        for(var i in data.BuildingInfo){
          if(data.BuildingInfo[i].BuildingID != String(buildingId)){
            //building idで絞り込む
            continue;//対象以外のデータは、スキップする
          }
          for(var j in data.BuildingInfo[i].Space.PeopleCount)
            {
              // Add time data to array
            Timedat.push(data.BuildingInfo[i].Space.PeopleCount[j].Time);
            //add people data to array
            ppldat.push(Number(data.BuildingInfo[i].Space.PeopleCount[j].TimeValue));
          } 
        }
        if(Timedat.length == 0){
          //データがある建物が選択されていないため、警告メッセージ
          alert("TestData.jsonにデータのある建物を選択してください。選択された建物のID：" + buildingId);
        }
      }

      var rtnBarData:ChartData<"bar">={
        labels:Timedat, //use time data from array
        datasets:[
            {
                label:"来店人数",
                data:ppldat, //use people data from array
                backgroundColor: ['rgba(54, 162, 235)'],
            },
        ],
      };   

      return rtnBarData;
    }

    //methodの定義
    /**
     * 建物のページを開く
     */
    const showWebPage =():void =>{
        window.open(state.bldgInfoUrl);
    };


    return { 
      state,
      showWebPage
    };
  },
 })


